<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      :fullscreen="this.$store.state.sys.isMobile"
      hide-overlay
      persistent
      transition="dialog-bottom-transition"
      width="500"
    >
      <v-card>
        <v-toolbar dark :color="this.$store.state.sys.color">
          <v-toolbar-title></v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click="isClose">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <!-- <iframe src="http://login.imzdx.top/#/Login" width="100%" height="600"></iframe> -->
        <v-card-text>
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-text-field
              v-model="user.name"
              :counter="10"
              :rules="nameRules"
              prepend-icon="mdi-account-circle"
              label=""
              required
            ></v-text-field>

            <v-text-field
              v-model="user.password"
              :counter="18"
              :rules="passwordRules"
              type="password"
              prepend-icon="mdi-key"
              label=""
              required
            ></v-text-field>

            <v-text-field
              v-model="user.email"
              :counter="20"
              :rules="emailRules"
              prepend-icon="mdi-email"
              label="email"
              required
            ></v-text-field>

            <v-text-field
              v-model="user.phone"
              :counter="14"
              :rules="phoneRules"
              prepend-icon="mdi-cellphone"
              label=""
              required
            ></v-text-field>
            <!-- <input type="date" name="birthday" v-model="user.birthday" /> -->
            <v-dialog
              ref="dialog"
              v-model="modal"
              :return-value.sync="user.birthday"
              
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="user.birthday"
                  label=""
                  prepend-icon="mdi-cake"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="user.birthday" scrollable locale="zh-cn">
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="modal = false">
                  
                </v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(user.birthday)">
                  
                </v-btn>
              </v-date-picker>
            </v-dialog>

            <v-checkbox
              v-model="checkbox"
              :rules="[(v) => !!v || '']"
              label=""
              required
            ></v-checkbox>

            <v-btn
              :disabled="!valid"
              color="success"
              class="mr-4"
              @click="validate"
              :loading="loading"
            >
              
            </v-btn>

            <v-btn color="error" class="mr-4" @click="reset">  </v-btn>

            <!-- <v-btn color="warning" @click="resetValidation">  </v-btn> -->
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar">
      {{ message }}
    </v-snackbar>
  </v-row>
</template>

<script>
export default {
  name: "Register",
  props: ["dialog"],
  data: () => ({
    modal:false,
    user: {
      name: "",
      password: "",
      email: "",
      phone: "",
      birthday: "2021-04-30",
    },
    valid: true,

    nameRules: [
      (v) => !!v || "",
      (v) => (v && v.length <= 10) || "10",
    ],

    passwordRules: [
      (v) => !!v || "",
      (v) => (v && v.length >= 6) || "6",
    ],
    phoneRules: [
      (v) => !!v || "",
      (v) => (v && v.length <= 14) || "14",
    ],
    emailRules: [
      (v) => !!v || "",
      (value) => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "";
      },
    ],
    checkbox: true,
    loading: false,
    message: "",
    snackbar: false,
  }),
  methods: {
    isClose() {
      this.$store.commit("dialog", "Register");
    },
    validate() {
      //
      this.$refs.form.validate();
      this.submit();
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    //Cookie
    setCookie(name, value, seconds) {
      seconds = seconds || 0; //seconds0php
      var expires = "";
      if (seconds != 0) {
        //cookie
        var date = new Date();
        date.setTime(date.getTime() + seconds * 1000);
        expires = "; expires=" + date.toGMTString();
      }
      //
      //document.cookie = name + "=" + escape(value) + expires + "; domain=.imzdx.top"; //
      document.cookie = name + "=" + escape(value) + expires + ";"; //
    },
    getCookie(cname) {
      var name = cname + "=";
      var decodedCookie = decodeURIComponent(document.cookie);
      var ca = decodedCookie.split(";");
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == " ") {
          c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
          return c.substring(name.length, c.length);
        }
      }
      return "";
    },

    submit() {
      this.loading = true;
      let data = new FormData();
      data.append("uname", this.user.name);
      data.append("password", this.user.password);
      data.append("email", this.user.email);
      data.append("phone", this.user.phone);
      data.append("birthday", this.user.birthday);
      console.log(data);
      this.$axios
        .post("/user/register", data)
        .then((response) => {
          let that = this;
          that.loading = false;
          if (response.data.code == 200) {
            that.message = response.data.meg + ",";
            that.snackbar = true;
            that.$store.commit("dialog", "Register");
            that.$store.commit("dialog", "Login");
          } else if (response.data.code == 201) {
            that.snackbar = true;
            that.message = "" + response.data.code;
          } else {
            that.snackbar = true;
            that.message = "" + response.data.code;
          }
        })
        .catch((failResponse) => {
          this.loading = false;
          this.snackbar = true;
          this.message = "" + failResponse;
        });
    },
  },
};
</script>